.rc-slider-mark-text {
  font-size: 1rem;
}

.rc-slider-mark-text-active {
  color: #FF8A65;
  font-size: 1.2rem;
}

.rc-slider-rail, .rc-slider-step {
  background-color: #cccccc;
}

.rc-slider-handle {
  height: 16px;
  width: 16px;
  margin-top: -6px;
  background-color: #FF8A65 !important;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Cantarell", "Droid Sans", "Helvetica Neue", sans-serif;
  color: white;
  background-color: #212121;
  /* background-color: #424242; */
}

main, nav {
  margin: 0 1rem;
}

nav {
  padding: 0.6rem 0;
  height: 5vh;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.row {
  display: flex;
  width: 100%;
  height: 46vh;
}

.row>div {
  flex-grow: 1;
  margin: 0;
  border: 1px dashed #888;
}

.control {
  border: none !important;
}

.control:nth-child(1) {
  width: 40%;
  padding-right: 1rem;
}

.control:nth-child(2) {
  width: 60%;
  overflow: hidden;
}

.control:nth-child(3) {
  text-align: center;
  width: 5%;
}

.idiom {
  box-sizing: border-box;
  padding: 0.4rem;
  height: 100%;
}

.idiom>div {
  width: 100%;
  height: 100%;
}

.idiom:nth-child(1) {
  width: 30%;
}

.idiom:nth-child(2) {
  width: 70%;
}
/* ========== COMMON ========== */

.tick text {
  fill: white;
}

.tick line {
  stroke: white;
}

/* ========== PLOT ========== */

.circles {
  fill: orangered;
  stroke: red;
  stroke-width: 1;
  opacity: 0.9;
}

.text {
  fill: white;
  font-family: sans-serif;
  font-size: 10px;
}

/* ========== LINES ========== */

.line {
  fill: none;
  stroke-width: 2;
}

.dots {
  stroke-width: 5;
}

/* ========== MAP ========== */

.countries {
  fill: black;
}

.countries path {
  stroke: transparent;
  opacity: 0.9;
  stroke-width: 0.4;
  stroke-opacity: 1;
}

.countries .selected {
  opacity: 1;
}

.countries path:hover {
  stroke-width: 1.2;
  opacity: 1;
}

.names {
  fill: none;
  stroke: white;
  stroke-linejoin: round;
}

.names:hover {
  fill: black;
}

/* ========== CHORD ========== */

.arcs {
  fill-opacity: 0.8;
}

path.fade {
  fill-opacity: .1;
  stroke-opacity: .1;
}

path.show {
  fill-opacity: 1;
  stroke-opacity: 1;
}
